/**
 * @param {HTMLElement} dom çœŸå®žDOM
 * @param {vnode} vnode è™šæ‹ŸDOM
 * @param {HTMLElement} container å®¹å™¨
 * @returns {HTMLElement} æ›´æ–°åŽçš„DOM
 */
export function diff(dom, vnode, container) {
  const ret = diffNode(dom, vnode);

  if (container && ret.parentNode !== container) {
    container.appendChild(ret);
  }

  return ret;

}

function diffNode(dom, vnode) {
  let out = dom;

  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = '';

  if (typeof vnode === 'number') vnode = String(vnode);

  // diff text node
  if (typeof vnode === 'string') {

    // å¦‚æžœå½“å‰çš„DOMå°±æ˜¯æ–‡æœ¬èŠ‚ç‚¹ï¼Œåˆ™ç›´æŽ¥æ›´æ–°å†…å®¹
    if (dom && dom.nodeType === 3) {    // nodeType: https://developer.mozilla.org/zh-CN/docs/Web/API/Node/nodeType
      if (dom.textContent !== vnode) {
        dom.textContent = vnode;
      }
      // å¦‚æžœDOMä¸æ˜¯æ–‡æœ¬èŠ‚ç‚¹ï¼Œåˆ™æ–°å»ºä¸€ä¸ªæ–‡æœ¬èŠ‚ç‚¹DOMï¼Œå¹¶ç§»é™¤æŽ‰åŽŸæ¥çš„
    } else {
      out = document.createTextNode(vnode);
      if (dom && dom.parentNode) {
        dom.parentNode.replaceChild(out, dom);
      }
    }

    return out;
  }

  if (typeof vnode.tag === 'function') {
    return diffComponent(dom, vnode);
  }

  //
  if (!dom || !isSameNodeType(dom, vnode)) {
    out = document.createElement(vnode.tag);

    if (dom) {
      [...dom.childNodes].map(out.appendChild);    // å°†åŽŸæ¥çš„å­èŠ‚ç‚¹ç§»åˆ°æ–°èŠ‚ç‚¹ä¸‹

      if (dom.parentNode) {
        dom.parentNode.replaceChild(out, dom);    // ç§»é™¤æŽ‰åŽŸæ¥çš„DOMå¯¹è±¡
      }
    }
  }

  if (vnode.children && vnode.children.length > 0 || (out.childNodes && out.childNodes.length > 0)) {
    diffChildren(out, vnode.children);
  }

  diffAttributes(out, vnode);

  return out;

}

function diffChildren(dom, vchildren) {
  const domChildren = dom.childNodes;
  const children = [];

  const keyed = {};

  // å°†æœ‰keyçš„èŠ‚ç‚¹å’Œæ²¡æœ‰keyçš„èŠ‚ç‚¹åˆ†å¼€
  if (domChildren.length > 0) {
    for (let i = 0; i < domChildren.length; i++) {
      const child = domChildren[i];
      const key = child.key;
      if (key) {
        keyedLen++;
        keyed[key] = child;
      } else {
        children.push(child);
      }
    }
  }

  if (vchildren && vchildren.length > 0) {

    let min = 0;
    let childrenLen = children.length;

    for (let i = 0; i < vchildren.length; i++) {
      // debugger
      const vchild = vchildren[i];
      const key = vchild.key;
      let child;
      
      // å¦‚æžœæœ‰keyï¼Œæ‰¾åˆ°å¯¹åº”keyå€¼çš„èŠ‚ç‚¹
      if (key) {

        if (keyed[key]) {
          child = keyed[key];
          keyed[key] = undefined;
        }
      
      // å¦‚æžœæ²¡æœ‰keyï¼Œåˆ™ä¼˜å…ˆæ‰¾ç±»åž‹ç›¸åŒçš„èŠ‚ç‚¹
      } else if (min < childrenLen) {

        for (let j = min; j < childrenLen; j++) {

          let c = children[j];

          if (c && isSameNodeType(c, vchild)) {

            child = c;
            children[j] = undefined;

            if (j === childrenLen - 1) childrenLen--;
            if (j === min) min++;
            break;

          }

        }

      }

      // å¯¹æ¯”
      child = diff(child, vchild);
      // æ›´æ–°DOM
      const f = domChildren[i];
      if (child && child !== dom && child !== f) {
        // å¦‚æžœæ›´æ–°å‰çš„å¯¹åº”ä½ç½®ä¸ºç©ºï¼Œè¯´æ˜Žæ­¤èŠ‚ç‚¹æ˜¯æ–°å¢žçš„
        if (!f) {
          dom.appendChild(child);
          // å¦‚æžœæ›´æ–°åŽçš„èŠ‚ç‚¹å’Œæ›´æ–°å‰å¯¹åº”ä½ç½®çš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸€æ ·ï¼Œè¯´æ˜Žå½“å‰ä½ç½®çš„èŠ‚ç‚¹è¢«ç§»é™¤äº†
        } else if (child === f.nextSibling) {
          removeNode(f);
          // å°†æ›´æ–°åŽçš„èŠ‚ç‚¹ç§»åŠ¨åˆ°æ­£ç¡®çš„ä½ç½®
        } else {
          // æ³¨æ„insertBeforeçš„ç”¨æ³•ï¼Œç¬¬ä¸€ä¸ªå‚æ•°æ˜¯è¦æ’å…¥çš„èŠ‚ç‚¹ï¼Œç¬¬äºŒä¸ªå‚æ•°æ˜¯å·²å­˜åœ¨çš„èŠ‚ç‚¹
          dom.insertBefore(child, f);
        }
      }

    }
  }

}

function diffComponent(dom, vnode) {
  let c = dom && dom._component;
  let oldDom = dom;
  // å¦‚æžœç»„ä»¶ç±»åž‹æ²¡æœ‰å˜åŒ–ï¼Œåˆ™é‡æ–°set props
  if (c && c.constructor === vnode.tag) {

    setComponentProps(c, vnode.attrs);
    dom = c.base;
    // å¦‚æžœç»„ä»¶ç±»åž‹å˜åŒ–ï¼Œåˆ™ç§»é™¤æŽ‰åŽŸæ¥ç»„ä»¶ï¼Œå¹¶æ¸²æŸ“æ–°çš„ç»„ä»¶
  } else {

    if (c) {
      unmountComponent(c);
      oldDom = null;
    }

    c = createComponent(vnode.tag, vnode.attrs);

    setComponentProps(c, vnode.attrs);
    dom = c.base;

    if (oldDom && dom !== oldDom) {
      oldDom._component = null;
      removeNode(oldDom);
    }

  }

  return dom;

}

function setComponentProps(component, props) {

  if (!component.base) {
    if (component.componentWillMount) component.componentWillMount();
  } else if (component.componentWillReceiveProps) {
    component.componentWillReceiveProps(props);
  }

  component.props = props;

  renderComponent(component);

}

export function renderComponent(component) {

  let base;

  const renderer = component.render();

  if (component.base && component.componentWillUpdate) {
    component.componentWillUpdate();
  }
  base = diffNode(component.base, renderer);

  component.base = base;
  base._component = component;

  if (component.base) {
    if (component.componentDidUpdate) component.componentDidUpdate();
  } else if (component.componentDidMount) {
    component.componentDidMount();
  }

  component.base = base;
  base._component = component;

}

function createComponent(component, props) {
  let inst;

  if (component.prototype && component.prototype.render) {
    inst = new component(props);
  } else {
    inst = new Component(props);
    inst.constructor = component;
    inst.render = function () {
      return this.constructor(props);
    }
  }

  return inst;

}

function unmountComponent(component) {
  if (component.componentWillUnmount) component.componentWillUnmount();
  removeNode(component.base);
}

function isSameNodeType(dom, vnode) {
  if (typeof vnode === 'string' || typeof vnode === 'number') {
    return dom.nodeType === 3;
  }

  if (typeof vnode.tag === 'string') {
    return dom.nodeName.toLowerCase() === vnode.tag.toLowerCase();
  }

  return dom && dom._component && dom._component.constructor === vnode.tag;
}

function diffAttributes(dom, vnode) {

  const old = {};    // å½“å‰DOMçš„å±žæ€§
  const attrs = vnode.attrs;     // è™šæ‹ŸDOMçš„å±žæ€§

  for (let i = 0; i < dom.attributes.length; i++) {
    const attr = dom.attributes[i];
    old[attr.name] = attr.value;
  }

  // å¦‚æžœåŽŸæ¥çš„å±žæ€§ä¸åœ¨æ–°çš„å±žæ€§å½“ä¸­ï¼Œåˆ™å°†å…¶ç§»é™¤æŽ‰ï¼ˆå±žæ€§å€¼è®¾ä¸ºundefinedï¼‰
  for (let name in old) {

    if (!(name in attrs)) {
      setAttribute(dom, name, undefined);
    }

  }

  // æ›´æ–°æ–°çš„å±žæ€§å€¼
  for (let name in attrs) {

    if (old[name] !== attrs[name]) {
      setAttribute(dom, name, attrs[name]);
    }

  }

}

function removeNode(dom) {

  if (dom && dom.parentNode) {
    dom.parentNode.removeChild(dom);
  }

}